.pricingCard {
    display: flex;
    flex-wrap: wrap;
    min-width: 250px;
    border-radius: 15px;
    box-shadow: 0px -4px 10px 7px rgba(0,0,0,0.09);
    > div {
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
        > div {
            display: flex;
            flex-basis: 100%;
            font-size: 0.8em;
            padding: 10px;
        }
        > button {
            border: none;
            background-color: #c49650c5;
            border-radius: 4px;
            padding: 10px;
            margin: 10px auto;
            font-weight: 700;
            color: white;
        }
    }
    > figure {
        display: flex;
        position: relative;
        flex-basis: 100%;
        margin: 0;
        .price {
            position: absolute;
            border-radius: 4px;
            top: 20px;
            right: 20px;
            color: white;
            font-weight: 500;
            color: white;
            padding: 5px 10px;
            background-color: #c49650c5;
        }
        > .name {
            position: absolute;
            top: 120px;
            background-color: white;
            padding: 10px;
            color: #c49650c5;
            font-weight: 700;
            border-top-right-radius: 5px;
        }
        > img {
            padding: 0;
            object-fit: cover;
            flex-basis: 100%;
            height: 150px;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
        }
    }
}