#pricing {
    max-width: 1200px;
    margin: 10px auto;
    display: flex;
    flex-wrap: wrap;
    flex-basis: 100%;
    color: rgba(0, 0, 0, 0.781);
    > h2 {
        font-weight: 600;
        font-size: 1.1em;
        padding: 0 20px;
        color: rgba(0, 0, 0, 0.822);
    }
    .pricing__cardContainer {
        padding: 20px 20px;
        max-width: 88vw;
        display: flex;
        flex-wrap: nowrap;
        overflow: scroll;
        column-gap: 30px;
    }
}

@media screen and (min-width: 672px){
    #pricing {
    
        .pricing__cardContainer {
            padding: 20px 20px;
            max-width: none;
            flex-basis: 100%;
            justify-content: space-between;
            display: flex;
            flex-wrap: nowrap;
            overflow: hidden;
        }
    }
}