@import '../../abstract/animation.scss';

#callToAction {
    display: flex;
    flex-basis: 100%;
    z-index: -1;

    > figure {
        display: flex;
        flex-basis: 100%;
        margin: 0;
        position: relative;
        .welcome-message {
            display: flex;
            position: absolute;
            width: 100%;
            height: 300px;
            background-color: white;
            top: 0;
            left: 0;
            opacity: 0;
            animation: cta-cach 5s ease-in-out 0s;
            justify-content: center;
            background-color: #ffffff1c;
            h1 {
                position: relative;
                right: 60px;
                justify-content: center;
                display: flex;
                flex-basis: 60%;
                margin-top: 130px;
                font-weight: 200;
                font-size:1.9em;
                padding: 2px;
                color: #c49650c5;
                animation: slide-top 1s ease-in 0s;
            }
        }
        > video {
            height: 300px;
            width: 100%;
            object-fit: cover;
        }
    }
}
@media screen  and (min-width: 672px){
    #callToAction > figure > video {
        height: 400px;
    }
    #callToAction > figure .welcome-message {
        height: 400px;
        h1 {
            font-size: 3.5em;
        }
    }
}
@media screen  and (min-width: 992px){
    #callToAction > figure > video {
        height: 400px;
    }
    #callToAction > figure .welcome-message {
        height: 400px;
        h1 {
            font-size: 5em;
            color: #c49650c5;
            font-weight: 100;
            position: relative;
            top: 150px;
            right: 100px;
        }
    }
}
@media screen  and (min-width: 1200px){
    #callToAction > figure > video {
        height: 600px;
    }
    #callToAction > figure .welcome-message {
        height: 600px;
        h1 {
            font-size: 5em;
            color: #c49650c5;
            font-weight: 100;
            position: relative;
            right: 20px;
        }
    }
}