#qualities {
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    margin: 20px auto;
    max-width: 1200px;
    padding: 0 20px;
    animation: slide-top 0.4s ease-in 1.3s both;
    > h2 {
        font-weight: 600;
        font-size: 1.1em;
        color: rgba(0, 0, 0, 0.822);
        text-align: center;
        margin: 0 auto;
    }
    .qualities_container {
        margin-top: 30px;
        display: flex;
        flex-basis: 100%;
        justify-content: space-between;
        > div {
            flex-direction: column;
            display: flex;
            flex-basis: 30%;
            justify-content: center;

            svg {
                font-size: 3em;
                margin: 0 auto;
                color: #c49650c5;
            }
            > h3 {
                font-size: 0.8em;
                display: flex;
                text-align: center;
                font-weight: 300;
            }
        }
    }
}
@media screen and (min-width: 672px) {
    #qualities {
        > h2 {
            font-size: 1.9em;
            margin: 20px auto;
        }
        .qualities_container  {
            flex-basis: 80%;
            margin: 20px auto;
            overflow: hidden;
            > div {
                
    
                svg {
                    font-size: 4em;
                    margin: 0 auto;
                    color: #c49650c5;
                }
                > h3 {
                    display: flex;
                    justify-content: center;
                    font-size: 1.2em;
                    display: flex;
                    text-align: center;
                    font-weight: 500;
                }
            }
        }
    }
}