@import '../../abstract/animation.scss';


header {
    display: flex;
    flex-basis: 100%;
    flex-wrap: wrap;
    position: fixed;
    top: 0;
    box-shadow: 0px -4px 40px 17px rgba(0,0,0,0.12);
    height: 70px;
    width: 100%;
    z-index: 999;
    background-color: white;
    > div:first-child {
        display: flex;
        flex-basis: 100%;
        max-width: 1200px;
        margin: 0 auto;
        position: relative;
        > a {
            display: flex;
            position: absolute;
            top: 0;
            left: 0;
            padding: 15px;
            background-color: white;
            border-bottom-right-radius: 20px;
            box-shadow: 0px -4px 40px 17px rgba(0,0,0,0.12);
            > img {
                height: 90px;
            }
            
        }
    }
    .nav_button {
        position: absolute;
        right: 20px;
        top: 15px;
        font-size: 0.65em;
        border: none;
        background-color: transparent;
        font-weight: 500;
        height: 50px;
        width: 50px;
        cursor: pointer;
        > span {
            height: 1.5px;
            width: 30px;
            background-color: #c49650c5;
            position: absolute;
            top: 20px;
            left: 10px;
            &::after {
                content: " ";
                height: 1.5px;
                width: 30px;
                position: absolute;
                top: -7px;
                left: 0px;
                background-color: #c49650c5;
            }
            &::before {
                content: " ";
                height: 1.5px;
                width: 30px;
                position: absolute;
                top: 7px;
                left: 0px;
                background-color: #c49650c5;
            }
        }
    }
    .nav_button-open {
        position: absolute;
        right: 20px;
        top: 15px;
        font-size: 0.65em;
        border: none;
        background-color: transparent;
        font-weight: 500;
        height: 50px;
        width: 50px;
        cursor: pointer;
        > span {
            height: 1.5px;
            width: 30px;
            background-color: white;
            position: absolute;
            top: 20px;
            left: 10px;
            
            &::after {
                content: " ";
                height: 1.5px;
                width: 30px;
                position: absolute;
                top: 1px;
                left: 0px;
                background-color: #c49650c5;
                transform: rotate(45deg);
                transition: transform 0.3s;
            }
            &::before {
                content: " ";
                height: 1.5px;
                width: 30px;
                position: absolute;
                top: 1px;
                left: 0px;
                background-color: #c49650c5;
                transform: rotate(-45deg);
                transition: all 0.3s;
            }
        }
    }
    .header__button-langtel-weather {
        display: flex;
        width: 140px;
        position: absolute;
        top: 27px;
        right: 100px;
        justify-content: space-between;
        > div:first-child {
            flex-basis: 50px;
            > div > img {
                height: 20px;
                object-fit: cover;
            }
        }
        > div:nth-child(2)> a {
            text-decoration: none;
            color: black;
            > svg {
                font-size: 1.6em;
                position: relative;
                bottom: 3px;
                color: #c49650c5;
            }
        }
        > div:nth-child(3) {
            display: flex;
            width: 50px;
            color: #c49650c5;
            position: relative;
            top: 5px;
            > svg {
                height: 20px;
                position: relative;
                bottom: 14px;
                left: 10px;
                color: #c49650c5;
            }
        }
    }
    .header_button-book {
        background-color: #c49650ee;
        position: absolute;
        top: 80px;
        left: 130px;
        display: flex;
        border-radius: 5px;
        
        > a {
            display: flex;
            align-items: center;
            text-align: center;
            padding: 10px 20px;
            text-decoration: none;
            font-weight: 600;
            color: white;
            font-size: 0.9em;
            border-radius:  5px;
        }
    }
}
.header-open {
    animation: display-nav 0.3s ease-in 0s both;
    height: 100vh;
}