@keyframes display-nav {
    0% {
        height: 70px;
      }
      100% {
        height: 100vh;
      }
}
@keyframes slide-top {
  0% {
    transform: translateY(-10px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes cta-cach {
  0% {
      opacity: 1;
    }
    80% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
}